body {
  display: flexbox;
  height: 853px;
  background-image: url("https://cdn.pixabay.com/photo/2018/05/01/07/52/tuscany-3364921_1280.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: Times New Roman, Times, serif;
  position: relative;
}

.clock {
  width: 560px;
  height: 560px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("https://i.ibb.co/dBGxSK1/hiclipart-com-3.png");
  background-size: cover;
  border-radius: 50%;
  margin-right: -50%;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translateX(-50%);
}

.clock-center {
  width: 20px;
  height: 20px;
  background-color: #000;
  border-radius: 50%;
  position: absolute;
  top: 268px;
  left: 268px;
}

.clock-seconds_arrow {
  width: 5px;
  height: 40%;
  bottom: 50%;
  left: 50%;
}

.clock-minutes_arrow {
  width: 7px;
  height: 33%;
  bottom: 50%;
  left: 50%;
}

.clock-hours_arrow {
  width: 12px;
  height: 24%;
  bottom: 50%;
  left: 50%;
}

.date {
  color: #000;
}

.calendar {
  justify-content: space-between;
  display: flex;
}

.date-style {
  text-shadow: 3px 3px 2px #cecbcd;
  font-size: 32px;
  font-weight: 700;
  display: flex;
}

.digital-clock {
  text-shadow: 3px 3px 2px #cecbcd;
  justify-content: center;
  margin: 12px auto;
  font-size: 40px;
  display: flex;
}

